import React, { useEffect, Suspense, lazy } from "react";
import { HashRouter as Router, Route, Switch, /* Redirect */ } from "react-router-dom";
import { useWallet } from "@binance-chain/bsc-use-wallet";
import { ResetCSS } from "@pancakeswap-libs/uikit";
import BigNumber from "bignumber.js";
import { useFetchPublicData } from "state/hooks";
import queryString from "query-string";
import { useCookies } from "react-cookie";
import GlobalStyle from "./style/Global";
import Menu from "./components/Menu";
import PageLoader from "./components/PageLoader";
import NftGlobalNotification from "./views/Nft/components/NftGlobalNotification";

// Route-based code splitting
const Home = lazy(() => import("./views/Home"));
const Farms = lazy(() => import("./views/Farms"));
const Pools = lazy(() => import("./views/Pools"));
const Referrals = lazy(() => import("./views/Referrals"));
const Products = lazy(() => import("./views/Products"));
const NotFound = lazy(() => import("./views/NotFound"));

// This config is required for number formating
BigNumber.config({
  EXPONENTIAL_AT: 1000,
  DECIMAL_PLACES: 80,
});

const App: React.FC = () => {
  const { account, connect } = useWallet();

  useEffect(() => {
    if (!account && window.localStorage.getItem("accountStatus")) {
      connect("injected");
    }
  }, [account, connect]);

  useFetchPublicData();

  const getParams = queryString.parse(window.location.search);
  const [, setCookie, ] = useCookies(["refer"]);
  // const [, setCookie, removeCookie] = useCookies(["refer"]);

  if ("refer" in getParams) {
    // Get the referal address from the parameter
    let referralAddress = getParams.refer as string;

    // Prepend 0x to the address since it will not contain it
    referralAddress = `0x${referralAddress}`;

    // Set a referral cookie if the user is logged in
    if (account) {
      setCookie("refer", referralAddress, {
        path: "/",
        expires: new Date(Date.now() + 2592000)
      });

      window.location.href = `https://beastmode.finance/`;
    }
  }

  return (
    <Router>
      <ResetCSS />
      <GlobalStyle />
      <Menu>
        <Suspense fallback={<PageLoader />}>
          <Switch>
            <Route path="/" exact>
              <Home />
            </Route>
            <Route path="/farms">
              <Farms />
            </Route>
            <Route path="/pools">
              <Pools />
            </Route>
            <Route path="/referrals">
              <Referrals />
            </Route>
            <Route path="/products">
              <Products />
            </Route>
            <Route component={NotFound} />
          </Switch>
        </Suspense>
      </Menu>
      <NftGlobalNotification />
    </Router>
  );
};

export default React.memo(App);
